.gifScreen{
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(54, 171, 255); 
}

.gifGrid{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.gifGrid__title{
  text-decoration: underline;
}

.giphyItem{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 25vw;
  height: 25vh;
  margin: 2%;
  box-shadow: 10px 5px 5px black;
  overflow: hidden;
  text-align: center;
 
}
  
.giphyItem__img{
  width: 80%;
  height: 80%;
}

.gifGrid__container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}